.page-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 1080px;
}

.welcome-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.welcome-page__title {
  font-size: 40px;
  font-weight: 600;
  line-height: 48px;
  margin: 80px 0 140px;
  padding: 0;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.welcome-page__logo {
  width: 580px;
  margin-top: 110px;
}

.welcome-page__text {
  color: var(--color-text-base);
  font-size: 25px;
  font-weight: 600;
  line-height: 30px;
  margin: 0 95px 30px 0;
  align-self: flex-end;
  cursor: pointer;
  background: none;
  border: none;
  letter-spacing: 0.02em;
}

.welcome-page__text::first-letter {
  color: var(--color-text-accent);
}
