.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form__fieldset {
  position: relative;
  border: none;
  padding: 0;
  margin: 0 0 150px;
  display: flex;
  gap: 20px;
}

.form__input {
  width: 550px;
  height: 65px;
  border-radius: 15px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.form__input::placeholder {
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 25px;
  line-height: 30px;
  color: var(--color-backgroiund-input);
  margin: 0;
  padding: 0;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.form__button {
  width: 250px;
  height: 65px;
  color: #fff;
  font-size: 25px;
  font-weight: 600;
  line-height: 30px;
  background-color: var(--color-backgroiund-base);
  border: none;
  border-radius: 15px;
}
